import FavouriteRestaurantIdb from "../data/favorite-resto"
import { createLikeButtonTemplate, createLikedButtonTemplate } from "../views/templates-api/template-detail-creator"
 
const LikeButtonInitiator = {
    async init({ likeButtonContainer, restaurant }) {
        this._likeButtonContainer = likeButtonContainer;
        this._restaurant = restaurant;
 
        await this._renderButton();
    },
 
    async _renderButton() {
        const { id } = this._restaurant;
 
        if (await this._isRestaurantExist(id)) {
            this._renderLiked();
        } else {
            this._renderLike();
        }
    },
 
    async _isRestaurantExist(id) {
        const restaurant = await FavouriteRestaurantIdb.getRestaurant(id);
        return !!restaurant;
    },
 
    _renderLike() {
        this._likeButtonContainer.innerHTML = createLikeButtonTemplate();
 
        const likeButton = document.querySelector("#likeButton");
        likeButton.addEventListener("click", async () => {
            await FavouriteRestaurantIdb.putRestaurant(this._restaurant);
            this._renderButton();
        });
    },
 
    _renderLiked() {
        this._likeButtonContainer.innerHTML = createLikedButtonTemplate();
 
        const likeButton = document.querySelector("#likeButton");
        likeButton.addEventListener("click", async () => {
            await FavouriteRestaurantIdb.deleteRestaurant(this._restaurant.id);
            this._renderButton();
        });
    },
};
 
export default LikeButtonInitiator;